.navbar {
  background-color: #1EC6CB22;
  width: 100%;
  height: 60px;

  @media (max-width: 650px) {
    height: 35px;
  }
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 10px;
  gap:100px;

  @media (max-width: 1200px) {
    gap: 50px;
  }
  
  @media (max-width: 650px) {
    gap: 20px;
  }

  @media (max-width: 450px) {
    gap: 10px;
  }
}

.link{
  color: #B290B6;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;

  @media (max-width: 1200px) {
    font-size: 18px;
  }
  
  @media (max-width: 650px) {
    font-size: 14px;
  }

  @media (max-width: 450px) {
    font-size: 10px;
  }
}

.active {
  color: #1EC6CB;
}


.nav-logo{
  height: 40px;
  width: 40px;
  margin-right: 10px;

  @media (max-width: 650px) {
    height: 15px;
    width: 15px;
    margin-right: 3px;
  }
}

.nav-logo-dreamcatcher{
  height: 50px;
  width: 50px;
  margin-right: 10px;

  @media (max-width: 650px) {
    height: 25px;
    width: 25px;
    margin-right: 0;
  }
}

.animated{
  animation-name: spin;
  animation-duration: 8000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}

.container-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 10px;
}
